$Brcolor: #653F4C/*коричневые цвета*/
$Becolor: #E5D4CD/*бежевые цвета*/
$Borcolor: #843D5B
body
    background-color: $Becolor
    max-width: 1900px
.header
    background-color: $Brcolor
    display: flex
    justify-content: space-between
    align-items: center
    width: 100%
    height: 73px
    flex-wrap: nowrap
    color: #E5D4CD
.header__logo
    align-self: stretch
    display: flex
    align-items: stretch
    padding-left: 26px
    a
        display: flex
        align-items: stretch
li
    display: inline-block
    padding: 50px
.home-page__line
    color: $Brcolor
    width: 100%
    height: 0px
    stroke-width: 1px
    stroke: #000
.menu-header__link
    text-decoration: none
    color: $Becolor
    font-family: Manrope
    font-size: 18px
    font-style: normal
    font-weight: 400
    text-transform: uppercase
.home-page__title
    font-size: 3vw
    font-family: "Crimson Pro"
    font-weight: 700
    color: $Brcolor
    margin-bottom: 0
.home-page
    display: flex
    padding: 20px
.home-page__text-area
    display: flex
    flex-direction: column
    width: 40vw
    padding-top: 80px
    margin-left: 100px
.home-page__title-area
    justify-content: center
.home-page__main-text
    display: flex
    flex-direction: column
    justify-content: end
    align-items: end
    padding-top: 180px
.home-page__text
    color: $Brcolor
    padding-top: 300px
    font-size: 16px
    font-style: normal
    font-weight: 400
    font-family: Manrope
    text-transform: uppercase

.home-page__subtitle
    color: $Borcolor
    font-family: Manrope
    font-size: 20px
    font-style: normal
    font-weight: 600
    line-height: 154.5% /* 30.9px */
    letter-spacing: 2px
    text-transform: uppercase
    margin-top: 0

.home-page__images
    max-width: 50%
    padding: 100px 30px
    width: 70%
    margin: 0 auto
    display: flex
    position: relative
.home-page__img
    position: relative
    padding-top: 30px
    padding-right: 20%
    width: 70%
    z-index: 2
.home-page__background
    position: absolute
    width: 72%
    top: 165px
    right: 150px
    border-radius: 0px 100px 0px 0px
    opacity: 0.25
    z-index: -1

.button
    display: flex
    width: 220px
    height: 60px
    justify-content: center
    align-items: center
    gap: 10px
    flex-shrink: 0
    border-radius: 25px
    border: 2px solid $Brcolor
    background-image: url("./images/arrow.svg")
    background-repeat: no-repeat
    background-size: 80% auto

.button__text
    text-align: start
    font-family: Manrope
    font-size: 1.1rem
    font-style: normal
    font-weight: 400
    line-height: 125% /* 25px */
    letter-spacing: 2.4px
    text-decoration-line: underline
    text-transform: uppercase
    color: $Brcolor
    margin-top: 100px
    width: 250px
footer
    display: flex
    align-items: center
    padding-top: 50px
    a
        padding: 10px
    .copyright
        padding: 10px
